<template>
  <view class="container">
    <view class="header">
      <view class="title-box">
        <view class="inner">
          <view class="title">正式课</view>
          <view class="desc">每一次练习,都在浇灌希望的种子</view>
        </view>
        <image src="../../static/images/image1.png" mode="" class="img"></image>
      </view>
    </view>
    <view class="form-box">
      <uni-forms
        ref="form"
        :modelValue="formData"
        label-width="160rpx"
        :label-align="labelPosition"
      >
        <view class="item-title">基本信息</view>
        <uni-forms-item label="姓名" required>
          {{ formData.name }}
        </uni-forms-item>
        <uni-forms-item label="手机号" required>
          {{ formData.phone }}
        </uni-forms-item>
        <uni-forms-item label="接送地址" required>
          <view class="inner" @click="selectAddress">
            <uni-easyinput
              disabled
              :disableColor="{ disableColor: '#f5f5f5' }"
              v-model="formData.address"
              placeholder="请选择定位"
            />
            <view class="icon-wrap">
              <image
                src="../../static/images/position.png"
                mode=""
                class="icon"
              ></image>
              <view class="text">定位</view>
            </view>
          </view>
        </uni-forms-item>
        <view class="item-title time-box"
          >预约时间
          <view class="total-time" v-show="totalTime">共{{ totalTime }} </view>
        </view>
        <uni-forms-item label="开始" required>
          <view class="inner" @click="showSelectDate('start')">
            <uni-easyinput
              disabled
              v-model="formData.startTime"
              placeholder="请选择开始时间"
            />
          </view>
          <view class="icon-wrap right">
            <uv-icon
              class="icon"
              name="arrow-down"
              color="#9FA2A8"
              size="16"
            ></uv-icon>
          </view>
        </uni-forms-item>
        <uni-forms-item label="结束" required>
          <view class="inner" @click="showSelectDate('end')">
            <uni-easyinput
              disabled
              v-model="formData.endTime"
              placeholder="请选择结束时间"
            />
          </view>
          <view class="icon-wrap right">
            <uv-icon
              class="icon"
              name="arrow-down"
              color="#9FA2A8"
              size="16"
            ></uv-icon>
          </view>
        </uni-forms-item>
        <view class="item-title" style="margin-top: 22px">备注</view>
        <uni-easyinput
          v-model="formData.notes"
          type="textarea"
          placeholder="请输入备注"
        />
      </uni-forms>
    </view>
    <view class="submit-box">
      <button type="primary" class="submit" @click="submit">提交</button>
    </view>
    <uv-datetime-picker
      ref="startTime"
      :minDate="minStarTime"
      :formatter="formatter"
      v-model="startTime"
      mode="datetime"
      @confirm="startConfirm"
    ></uv-datetime-picker>
    <uv-datetime-picker
      ref="endTime"
      :minDate="startTime"
      :formatter="formatter"
      v-model="endTime"
      mode="datetime"
      @confirm="endConfirm"
    ></uv-datetime-picker>
  </view>
</template>

<script>
import { timeFormat } from "@/uni_modules/uv-ui-tools/libs/function/index.js";
import { mapGetters } from "vuex";
import { post } from "@/utils/request.js";
export default {
  data() {
    return {
      labelPosition: "left",
      imgData: null, //上传的驾驶证
      //表单数据
      formData: {
        name: "李鑫鑫", //姓名
        phone: "13212345678", //手机号
        address: "A小区-东门", //接送地址
        startTime: "2024-09-19 12:30", //开始时间
        endTime: "2024-09-19 12:30", //结束时间
        latitude: "", //纬度
        longitude: "", //经度
        notes: "哈哈哈，我是备注", //备注
      },
      startTime: "", //开始时间
      endTime: "", //结束时间
      minStarTime: "", //开始选择日期最小限制
      minEndTime: "", //结束选择日期最小限制
      maxEndTime: "", //结束选择日期最大限制
      //格式化时间选择
      formatter(type, value) {
        if (type === "year") {
          return `${value}年`;
        }
        if (type === "month") {
          return `${value}月`;
        }
        if (type === "day") {
          return `${value}日`;
        }
        if (type === "hour") {
          return `${value}时`;
        }
        if (type === "minute") {
          return `${value}分`;
        }
        return value;
      },
    };
  },
  computed: {
    totalTime() {
      if (!!this.formData.startTime && !!this.formData.endTime) {
        const start = new Date(this.formData.startTime.replace(/-/g, "/"));
        const end = new Date(this.formData.endTime.replace(/-/g, "/"));
        const difference = end - start;
        const hours = Math.floor(difference / (1000 * 60 * 60));
        const minutes = Math.floor(
          (difference % (1000 * 60 * 60)) / (1000 * 60)
        );
        return `${hours < 10 ? "0" + hours : hours}时${
          minutes < 10 ? "0" + minutes : minutes
        }分`;
      }
      return "";
    },
    ...mapGetters(["userinfo"]),
  },
  onLoad() {},
  methods: {
    //开始选择日期确认
    startConfirm(e) {
      this.formData.startTime = timeFormat(e.value, "yyyy-mm-dd hh:MM");
      this.formData.endTime = "";
      this.endTime = "";
    },
    //结束选择日期确认
    endConfirm(e) {
      this.formData.endTime = timeFormat(e.value, "yyyy-mm-dd hh:MM");
    },
    //显示选中时间弹窗
    showSelectDate(name) {
      if (name === "start") {
        this.minStarTime = +new Date();
        this.$refs.startTime.open();
      }
      if (name === "end") {
        if (!this.formData.startTime) {
          uni.showToast({
            title: "请选择开始时间",
            icon: "none",
          });
          return;
        }
        this.$refs.endTime.open();
      }
    },
    //提交
    submit() {
      if (!this.formData.name) {
        uni.showToast({
          title: "请输入姓名",
          icon: "none",
        });
        return;
      }
      if (!this.formData.phone) {
        uni.showToast({
          title: "请输入手机号",
          icon: "none",
        });
        return;
      }
      const reg = /^1[3-9]\d{9}$/;
      if (!reg.test(this.formData.phone)) {
        uni.showToast({
          title: "手机号格式错误",
          icon: "none",
        });
        return;
      }
      if (!this.formData.address) {
        uni.showToast({
          title: "请选择接送地址",
          icon: "none",
        });
        return;
      }
      if (!this.formData.startTime) {
        uni.showToast({
          title: "请选择接预约开始时间",
          icon: "none",
        });
        return;
      }
      if (!this.formData.endTime) {
        uni.showToast({
          title: "请选择接预约结束时间",
          icon: "none",
        });
        return;
      }
      const data = {
        openId: uni.getStorageSync("openid"),
        studentId: this.userinfo.id,
        startTime: this.formData.startTime + ":00",
        endTime: this.formData.endTime + ":00",
        longitude: this.formData.longitude,
        latitude: this.formData.latitude,
        address: this.formData.address,
        stuName: this.formData.name,
        telephone: this.formData.phone,
        notes: this.formData.notes,
      };
      uni.showLoading({
        title: "提交中...",
        mask: true,
      });
      return post("/lessonManagement/classReservation", data)
        .then((res) => {
          uni.showToast({
            title: "提交成功",
            duration: 1000,
            mask: true,
          });
          setTimeout(() => {
            uni.redirectTo({
              url: "/pages/index/index",
            });
          }, 1000);
          this.formData.name = ""; //姓名
          this.formData.phone = ""; //手机号
          this.formData.address = ""; //接送地址
          this.formData.startTime = ""; //开始时间
          this.formData.endTime = ""; //结束时间
          this.formData.latitude = ""; //纬度
          this.formData.longitude = ""; //经度
          this.formData.notes = ""; //备注
          this.startTime = ""; //开始时间
          this.endTime = ""; //结束时间
          this.minStarTime = ""; //开始选择日期最小限制
          this.minEndTime = ""; //结束选择日期最小限制
          this.maxEndTime = ""; //结束选择日期最大限制
        })
        .catch((err) => {
          uni.showToast({
            title: "提交失败",
            icon: "none",
            duration: 2000,
          });
          console.log(err);
        });
    },
    //开始时间发生变化
    handleStartChange() {
      this.formData.endTime = "";
    },
    async getLocationAndSelectAddress() {
      const _this = this;
      // 获取用户当前位置
      uni.getLocation({
        type: "gcj02",
        highAccuracyExpireTime: true,
        success: (result) => {
          // 打开选择地址界面
          uni.chooseLocation({
            latitude: result.latitude,
            longitude: result.longitude,
            success: (res) => {
              _this.formData.address = res.name;
              _this.formData.latitude = res.latitude;
              _this.formData.longitude = res.longitude;
            },
            fail: (err) => {
              console.log(err);
            },
          });
        },
        fail: (err) => {
          // 检测用户是否授权位置权限
          uni.getSetting({
            success(res) {
              if (!res.authSetting["scope.userLocation"]) {
                // 用户未授权，提示用户
                uni.showModal({
                  title: "需要定位权限",
                  content: "我们需要您的位置权限来提供服务",
                  success: (modalRes) => {
                    if (modalRes.confirm) {
                      // 用户同意，打开设置页面
                      uni.openSetting({
                        success(settingRes) {
                          if (settingRes.authSetting["scope.userLocation"]) {
                            _this.getLocationAndSelectAddress();
                          } else {
                            // 用户依然拒绝授权，可以再次提示或者提供替代方案
                            uni.showToast({
                              title: "需要您授权位置权限",
                              icon: "none",
                            });
                          }
                        },
                      });
                    }
                  },
                });
              }
            },
          });
        },
      });
    },
    //选择地址
    selectAddress() {
      this.getLocationAndSelectAddress();
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
  padding-bottom: 120rpx;
  line-height: 1;

  .header {
    box-sizing: border-box;
    width: 100%;
    height: 280rpx;
    padding: 0 28rpx;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #ebf8fb;

    .title-box {
      display: flex;
      align-items: center;
      justify-content: space-between;

      .inner {
        padding: 0 20rpx;

        .title {
          margin-bottom: 20rpx;
          color: #000000;
          font-size: 44rpx;
          font-weight: 600;
        }

        .desc {
          color: #64b0d2;
          font-size: 28rpx;
        }
      }

      .img {
        width: 242rpx;
        height: 242rpx;
      }
    }
  }

  /deep/ .form-box {
    padding: 48rpx 24rpx;

    .time-box {
      justify-content: space-between;
    }

    .total-time {
      color: #999999;
      text-align: left;
      font-size: 26rpx;
      position: relative;
    }

    .upload-box {
      padding: 20rpx 20rpx;
      border-radius: 12rpx;
      background-color: #f5f5f5;

      .preview {
        display: flex;
        align-items: center;
        height: 72rpx;

        .image {
          flex: 0 0 72rpx;
          height: 72rpx;
          margin-right: 12rpx;
        }

        .center {
          color: #1c1f1c;
          font-size: 28rpx;

          .size {
            margin-top: 5rpx;
            color: #595b59;
            font-size: 20rpx;
          }
        }

        .delete {
          width: 36rpx;
          height: 36rpx;
          margin-left: auto;
        }
      }

      .upload {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 72rpx;

        .icon {
          width: 30rpx;
          height: 30rpx;
          margin-right: 12rpx;
        }

        .text {
          color: #1d1e22;
          font-size: 28rpx;
          font-weight: bold;
        }
      }
    }

    .item-title {
      display: flex;
      align-items: center;
      margin-bottom: 42rpx;
      color: #000000;
      font-size: 30rpx;
      font-weight: bold;

      .text {
        margin-left: auto;
        color: #6c6c6d;
        font-size: 28rpx;
        font-weight: 0;
      }
    }

    .inner {
      position: relative;
      padding-right: 80rpx;
      background: #f5f5f5;
      border-radius: 15rpx;
    }

    .icon-wrap {
      position: absolute;
      right: 15rpx;
      top: 50%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      transform: translateY(-50%);

      &.right {
        right: 22rpx;
      }

      .icon {
        width: 24rpx;
        height: 24rpx;
      }

      .text {
        color: #2878ff;
        font-size: 28rpx;
        transform: scale(0.6);
      }
    }

    .uni-forms-item__label,
    .uni-easyinput__content-input {
      height: 92rpx;
      line-height: 92rpx;
      font-size: 30rpx !important;
    }

    .uni-date,
    .uni-date-x {
      border-radius: 12rpx;
      overflow: hidden;
    }

    .uni-date-x--border,
    .is-input-border {
      border-color: #f5f5f5 !important;
      border-radius: 12rpx;
    }

    .is-focused {
      border-color: #96d2f0 !important;
    }

    .is-disabled .uni-easyinput__placeholder-class,
    .is-disabled {
      background-color: #f5f5f5 !important;
      color: #999;
    }

    .uni-date-x,
    .uni-date-editor,
    .uni-easyinput__content {
      background-color: #f5f5f5 !important;
    }
  }

  .submit-box {
    z-index: 10;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 30rpx 28rpx;
    box-shadow: -5rpx 0 10rpx rgba(0, 0, 0, 0.1);
    background: #ffffff;

    .submit {
      border-radius: 50rpx;
      background: #007aff;
    }
  }
}
</style>
